<template>
  <div class="cart">
    <div class="carthead">
      <div class="topItem">
        <van-icon name="passed" />100%正品保证
      </div>
      <div class="topItem">
        <van-icon name="passed" />所有商品精挑细选
      </div>
      <div class="topItem">
        <van-icon name="passed" />售后无忧
      </div>
    </div>
    <div class="manage">
      <div class="buySum">
        购买数量
        <span class="buyNum">{{totalNum}}</span>
      </div>
      <div v-show="valid.length >= 1" @click="mang">{{ active ? "管理" : "取消" }}</div>
    </div>
    <div class="cartList">
      <div class="cartItem" v-for=" (v,index) in valid" :key="v.id">
        <div class="buyChechbox">
          <van-checkbox
            checked-color="#ee0a24"
            :name="index"
            v-model="v.isSelect"
            @change="itemSelcet"
          ></van-checkbox>
        </div>
        <div class="buyItem">
          <div class="buyImg">
            <img :src="v.productInfo.image" />
          </div>
          <div class="buyTitle">
            <div class="cartName">{{v.productInfo.store_name}}</div>
            <div class="attribute">属性:{{v.productInfo.attrInfo.suk}}</div>
            <div class="cartMony">￥{{v.truePrice}}</div>
          </div>
          <div class="add">
            <van-stepper v-model="v.cart_num" />
          </div>
        </div>
      </div>
    </div>

    <van-submit-bar
      :price="totalMoney*100"
      button-text="提交订单"
      @submit="onSubmit"
      class="topay"
      v-if="active"
    >
      <div>
        <div>
          <van-checkbox v-model="checkedAll" @click="selectAll">全选</van-checkbox>
        </div>
        <!-- <van-button type="primary" @click="checkAll">全选</van-button> -->
      </div>
    </van-submit-bar>
    <van-submit-bar class="show" v-if="!active">
      <template #button>
        <button class="btn" @click="deletes">删除</button>
      </template>
      <van-checkbox v-model="checkedAll" @click="selectAll">全选</van-checkbox>
      <button class="btn" @click="Collection">收藏</button>
    </van-submit-bar>
    <div v-if="valid.length< 1">
      <Recommend></Recommend>
    </div>
  </div>
</template>
<script>
import Recommend from "@/components/Recommen";
import { mapGetters } from "vuex";
export default {
  name: "Cart",
  data() {
    return {
      valid: [],
      result: [],
      checked: [],
      msg: [],
      infoList: "",
      checkedAll: false,
      orders: [],
      active: true,
    };
  },
  components: {
    Recommend,
  },
  computed: {
    //获取全局变量的token
    ...mapGetters(["token"]),
    // 计算商品总和
    totalMoney: function () {
      return this.valid
        .filter((item) => item.isSelect)
        .reduce((total, item) => {
          return total + item.cart_num * Number(item.truePrice);
        }, 0);
    },
    totalNum: function () {
      return this.valid
        .filter((item) => item.isSelect)
        .reduce((total, item) => {
          return total + item.cart_num;
        }, 0);
    },
  },
  created() {
    // 初始化购物车数据
    this.getCart();
  },
  methods: {
    //收藏
    Collection() {
      let ids = this.valid.map((e) => {
        return e.isSelect == true ? e.product_id : "";
      });
      console.log(ids);
      this.axios
        .post("api/collect/all", {
          category: "product",
          id: ids,
        })
        .then((data) => {
          console.log(data);
          this.$toast("收藏成功");
        });
    },
    //删除
    deletes() {
      let ids = this.valid.map((e) => {
        return e.isSelect == true ? e.id : "";
      });
      this.axios
        .post("http://47.115.51.185/api/cart/del?ids=" + ids)
        .then(() => {
          this.getCart();
        });
    },
    //购物车管理
    mang() {
      this.active = !this.active;
    },
    // 获取购物车数据
    getCart() {
      this.$axios
        .get("/api/cart/list", {
          params: {
            token: this.token,
          },
        })
        .then((data) => {
          if (data.data.status == 200) {
            this.valid = data.data.data.valid.map((n) => {
              let key = "isSelect";
              let value = false;
              n[key] = value;
              return n;
            });
          } else {
            this.$toast("数据加载错误");
            this.$router.push("/login");
          }
        });
    },
    // 全选计算函数
    selectAll() {
      this.valid.forEach((item) => {
        !this.checkedAll ? (item.isSelect = false) : (item.isSelect = true);
      });
    },
    // 单个复选框的计算
    itemSelcet() {
      let arrTrue = [];
      let arrFalse = [];
      let submitMsg = [];
      this.valid.forEach((m) => {
        if (m.isSelect) {
          arrTrue.push(m.isSelect);
        } else {
          arrFalse.push(m.isSelect);
        }
      });
      this.orders = this.valid
        .filter((e) => e.isSelect)
        .map((d) => {
          return d.id;
        });
      // 获取选中的商品id
      this.valid.forEach((m) => {
        if (m.isSelect == true) {
          submitMsg.push(m);
          this.$store.commit("setsubMsg", submitMsg);
        }
      });
      //当arrTrue 长度等于购物车数据列表长度时 说明全部选中
      if (arrTrue.length == this.valid.length) {
        this.checkedAll = true;
      } else {
        this.checkedAll = false;
      }
    },
    //跳转到结算页
    onSubmit() {
      if (this.orders == 0) {
        this.$toast("请选择产品");
      } else {
        this.$router.push(`/orders/submit/${this.orders}`);
      }
    },
  },
};
</script>
